import {JQLoader} from "./lib/JQLoader";

window.addEventListener("load", () => {
	document.querySelector(".send-feedback").addEventListener("click", () => {
		JQLoader.getLib(jQ => {
			let text = jQ(".feedback").val();
			jQ("body").append(`<div>${text}</div>`);
			jQ(".feedback").val("")
		})
	});
});


